.node {
    width:20px;
    height: 20px;
    border-left: 1px solid rgba(160, 228, 255, 0.8);
    border-top: 1px solid rgba(160, 228, 255, 0.8);
    display: inline-block;
}

.grid-row{    
    height: 20px ;
}

.grid {
    border-bottom: 1px solid rgba(160, 228, 255, 0.8);
    border-right: 1px solid rgba(160, 228, 255, 0.8);
}


.node-aStar-path {
    animation-name: aStarPath;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
}

@keyframes aStarPath {
    0% {
      transform: scale(0.6);
      background-color: gold;
      opacity: 0.2;

    }
  
    50% {
      transform: scale(1.2);
      background-color: gold;
      opacity: 0.5;
    }
  
    100% {
      transform: scale(1);
      background-color: rgb(255, 238, 0);
      opacity: 0.9;
    }
  }

  .node-dijkstra-path {
    animation-name: dijkstraPath;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
}

@keyframes dijkstraPath {
    0% {
      transform: scale(0.6);
      background-color: gold;
      opacity: 0.2;

    }
  
    50% {
      transform: scale(1.2);
      background-color:gold;
      opacity: 0.5;
    }
  
    100% {
      transform: scale(1);
      background-color: gold;
      opacity: 0.7;
    }
  }

.node-open {
    animation-name: open;
    animation-duration: 0.5s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
}

@keyframes close {
    0% {
        transform: scale(1);
        background-color: lavender;
        opacity: 0.5;
  
      }
    
      50% {
        transform: scale(1.2);
        background-color: lightblue;
        opacity: 0.7;
  
      }
    
      100% {
        transform: scale(1);
        background-color: lavender;
        opacity: 0.9;
  
      }
  }

.node-close {
    animation-name: close;
    animation-duration: 0.5s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
}

@keyframes open {
    0% {
      transform: scale(1);
      background-color: plum;
      opacity: 1;
    }
  
    50% {
      transform: scale(0.5);
      background-color: plum;
      opacity: 0.5;

    }
  
    100% {
      transform: scale(1);
      background-color: lightgrey;
      opacity: 0.7;

    }
}

.node-wall {
    background-color: rgba(0, 0, 0, 0.678);
    /* animation-name: wall;
