  .node-neighbor{
    animation-name: neighbor;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
}

@keyframes neighbor {
    0% {
      transform: scale(0.7);
      background-color: skyblue;
    }
  
    50% {
      transform: scale(0.4);
      background-color: skyblue;
    }
  
    100% {
      transform: scale(1);
      background-color: skyblue;
    }
  }



  .node-visited  {
    animation-name: visited;
    animation-duration: 1s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
}

@keyframes visited {
    0% {
      transform: scale(1);
      background-color: lavender;
      opacity: 0.5;

    }
  
    50% {
      transform: scale(1.2);
      background-color: lightblue;
      opacity: 0.7;

    }
  
    100% {
      transform: scale(1);
      background-color: lightblue;
      opacity: 0.6;

    }
  }


  .node-current  {
    animation-name: current;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
}

@keyframes current {
    0% {
      transform: scale(0.8);
      background-color: orange;
    }
  
    50% {
      transform: scale(1.2);
      background-color: orange;
    }
  
    100% {
      transform: scale(1);
      background-color: orange;
    }
  }






